.mainBlock {
    width: 640px;
    margin: 0 auto;
    padding: 12px;
}
.block {
    height: 166px;
    width: 100%;
    border-radius: 8px;
    background-color: var(--dark-1c);
    display: flex;
    margin: 15px 0;
}
.coubBlock, .block_1, .block_2, .block_3, .block_4, .textBlock, .actionBlock{
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: center;
    justify-content: start;
}
.block_1, .block_2, .block_3, .block_4, .block_0 {
    justify-content: space-around;
    min-width: 175px;
    text-align: center;
}
.textBlock {
    text-align: left;
    align-items: unset;
    margin: 15px;
    min-width: 237px;
} 
.title {
    font-size: 15px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.44px;
    color: #E5E4FA;
}
.description{
    font-size: 13px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.25px;
    color: var(--dark-E5);
    opacity: 0.6;
    max-width: 346px;
}
.actionBlock {
    width: 210px;
    margin: 15px;
}
.actionBlock button {
    width: 100%;
    height: 32px;
    font-size: 13px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.25px;
    text-align: center;
    margin: 4px 0;
    background-color: transparent;
    border-radius: 4px;
    border: 1px solid #6E42CA;
    color: #6E42CA;
}
.actionBlock button:hover{
    background-color: #6E42CA;
    color:var(--white);
}
.imgCoub {
    width: 59px;
    height: 64px;
}

.block_1 img{
    width: 80px;
    height: 80px;
    margin: 15px;
}

.block_4 img {
    width: 47px;
    height: 47px;
}

.block_2 img{
    width: 59px;
    height: 64px;
}
.block_3 img{
    width: 61px;
    height: 61px;
}
.block_0 .coub {
    width: 80px;
    height: 80px;
}
.cubeText {
    text-align: center;
    height: 18px;
    text-transform: uppercase;
    width: 175px;
    font-size: 10px;
    line-height: 16px;
    letter-spacing: 1.5px;
    color: var(--violet-8D);
}
.coub {
    display: flex;
}
.first {
    transform: translate(22px, 40px);
    z-index: 40;
 }
 .second {
    transform: translate(2px, 25px);
    z-index: 30;
 }
 .third {
    transform: translate(-18px, 10px);
    z-index: 20;
 }
 .forth {
    transform: translate(-40px, -6px);
    z-index: 10;
 }

 .block_1 .first {
    transform: translate(0px, 6px);
 }
 .block_2 .first {
    transform: translate(13px, 33px);
 }
 .block_2 .second {
    transform: translate(-11px, 15px);
 }
 .block_3 .first {
    transform: translate(15px, 40px);
 }
 .block_3 .second {
    transform: translate(-12px, 23px);
 }
 .block_3 .third {
    transform: translate(-40px, 6px);
 }
 .buttonFilter {
    width: 90px;
    height: 24px;
    border: 0;
    border-radius: 99px;
    margin: 15px 3px;
    color: var(--lviolet);
    opacity: 0.6;
    background-color: var(--dark-1c);
    cursor: pointer;
 }
 button.buttonFilter.active {
    color: var(--white);
    opacity: 1;
 }
 button.selected {
    background-color: var(--violet-8D);
    color: var(--white);
 }
 .warningText {
    color: var(--lviolet);
    font-size: 13px;
    opacity: 0.6;
 }