.searchForm {
    width: 100%;
    min-width: 200px;
    position: relative;
  }

  .inputContainer {
    position: relative;
    width: 100%;
    height: 40px;
  }
  
  .searchInput {
    font-size: 13px;
    letter-spacing: 0.25px;
    line-height: 20px;
    padding-left: 48px;
    height: 100%;
    width: 100%;
    border: 0;
    border-radius: 6px;
    color: var(--lviolet);
    opacity: 0.6;
    background-color: var(--dark-1c);
  }
  
  .searchInput::placeholder {
    color: var(--lviolet);
    opacity: 0.6;
  }

  .searchInput:focus-visible {
    outline: 0;
  }

  .button {
    background-color: transparent;
    border: none;
    position: absolute;
    cursor: pointer;
    padding: 0;
    margin: 0;
  }

  .button img {
    opacity: 0.4;
  }
  
  .searchButton {
    width: 20px;
    height: 26px;
    top: calc(50% - 10px);
    left: 16px;
  } 

  .highlighted {
    color: var(--yellow);
    font-size: inherit;
    font-weight: 400;
  }
  