.componentHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 600;
    margin: 0;
    padding: 12px;
    height: 80px;
  }

.logo {
    width: 143px;
    height: 40px;
    margin: 15px 20px;
}

.headerInput {
    background-color: transparent;
    height: 18px;
    text-transform: uppercase;
    width: 175px;
    font-size: 15px;
    opacity: 0.6;
    border: 0;
    box-shadow: none;
    color: var(--lviolet);
}

input.headerInput:focus {
    border: 0;
    box-shadow: none;
    border-bottom: thin solid var(--lviolet);
    outline: transparent;
}